<template>
  <v-container>
    <v-stepper v-model="e1">
      <v-stepper-header>
        <v-stepper-step
          :complete="e1 > 1"
          @click="e1 = 1"
          step="1"
        >Данные</v-stepper-step>

        <v-divider></v-divider>

        <v-stepper-step
          :complete="e1 > 2"
          @click="e1 = 2"
          step="2"
        >Пароль</v-stepper-step>

        <v-divider></v-divider>
      </v-stepper-header>

      <v-stepper-items>
        <v-stepper-content step="1">
          <v-card
            class="mb-12"
            height="200px"
          >
            <v-row>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">ФИО</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="name"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Должность</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="position"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">E-mail</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="email"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card>

          <v-btn
            color="primary"
          >
            Сохранить
          </v-btn>
        </v-stepper-content>

        <v-stepper-content step="2">
          <v-card
            class="mb-12"
            height="200px"
          >
            <v-row>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Текущий пароль</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="currentPassword"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Новый пароль</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="newPassword"
                ></v-text-field>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="4"
              >
                <p class="title">Подтвердить пароль</p>
              </v-col>
              <v-col
                class="pt-0 pb-0"
                cols="8"
              >
                <v-text-field
                  dense
                  v-model="acceptPassword"
                ></v-text-field>
              </v-col>
            </v-row>
          </v-card>

          <v-btn
            color="primary"
            to="/sales-channels/list"
          >
            Сохранить
          </v-btn>

          <v-btn text>Выход</v-btn>
        </v-stepper-content>

      </v-stepper-items>
    </v-stepper>
  </v-container>
</template>

<script>
  export default {
    name: 'CreateSource',
    data () {
      return {
        e1: 1,
        currentPassword: '',
        newPassword: '',
        acceptPassword: '',
        name: '',
        position: '',
        email: '',
      }
    },
  }
</script>

<style scoped>
  .title {
    font-size: 18px !important;
    font-weight: 500 !important;
    color: orange !important;
    padding-bottom: 0 !important;
    padding-top: 0 !important;
  }
</style>
